import BbwzConstDefine, { BbwzGameState } from "../data/BbwzConstDefine";
import BbwzBaseView from "./BbwzBaseView";
import { BbwzTimeAutoRun } from "../component/BbwzTimeAutoRun";

/**
 * 游戏层 状态控制组件
 */
export default class BbwzStateView extends BbwzBaseView {
    private betStartNode: cc.Node;
    private betEndNode: cc.Node;
    private betTimeNode: cc.Node
    private timeRunComp: BbwzTimeAutoRun;
    private betStartSk: sp.Skeleton;
    private betEndSk: sp.Skeleton;


    constructor(node: cc.Node) {
        super();
        this.setNode(node);
    }

    initView() {
        this.betStartNode = this.getChild("state_1");
        this.betStartSk = <sp.Skeleton>this.getComponent("state_1/spine", sp.Skeleton);
        this.betEndNode = this.getChild("state_3");
        this.betEndSk = <sp.Skeleton>this.getComponent("state_3/spine", sp.Skeleton);

        this.betTimeNode = this.getChild("state_2");
        this.timeRunComp = <BbwzTimeAutoRun>Global.UIHelper.safeGetComponent(this.betTimeNode, "timeLbl", BbwzTimeAutoRun);
        this.timeRunComp.node.active = false;
    }

    public runState(gameState: number, isPlayAnim: boolean = true){
        this.active = true;
        this.betStartNode.active = gameState == BbwzGameState.BetStart;
        this.betEndNode.active = gameState == BbwzGameState.BetEnd;
        this.betTimeNode.active = gameState == BbwzGameState.Bet;
        switch (gameState) {
            case BbwzGameState.BetStart:
                if (isPlayAnim)
                    this.playStartAnim();
                break;
            case BbwzGameState.BetEnd:
                if (isPlayAnim)
                    this.playEndAnim();
                break;
            case BbwzGameState.Bet:
                break;
        }
    }

    private playStartAnim(){
        this.betStartSk.setAnimation(0, "idle", false);
        let tween: cc.Tween = Game.Tween.get(this.betStartNode);
        tween.to(0.25, { position: cc.v2(0, this.betStartNode.y) }, null)
            .delay(1.0)
            .to(0.25, { position: cc.v2(1200, this.betStartNode.y) }, null)
            .start();
        Global.Audio.playBundleSound(BbwzConstDefine.GAME_ID,BbwzConstDefine.SOUND_BET_START, true);
    }

    private playEndAnim(){
        this.betEndSk.setAnimation(0, "idle", false);
        let tween: cc.Tween = Game.Tween.get(this.betEndNode);
        tween.to(0.25, { position: cc.v2(0, this.betEndNode.y) }, null)
            .delay(1.0)
            .to(0.25, { position: cc.v2(1200, this.betEndNode.y) }, null)
            .start();
        Global.Audio.playBundleSound(BbwzConstDefine.GAME_ID,BbwzConstDefine.SOUND_BET_END, true);
    }

    public setTimeRunConfig(leftTime: number, callback?: Function, target?: any) {
        this.timeRunComp.node.active = true;
        this.timeRunComp.setTimer(leftTime);
        this.timeRunComp.setSecondCall(callback, target);
    }

    protected onClose(){
        this.timeRunComp.node.active = false;
    }

    public clearByRound() {
        this.active = false;
        this.betStartNode.active = false;
        this.betEndNode.active = false;
        this.betTimeNode.active = false;
    }
}
